.contact-us {

  &__info {
    margin: 0 0 24px;

    &:first-child {
      margin-top: 30px;

      @include onTablet {
        margin-top: 0;
      }

      @include onDesctop {
        margin-top: 0;
      }
    }
  }

  &__info-title {
    margin: 0 0 8px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #9FADC2;
  }

  &__info-link {
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #3E3E3E;
    text-decoration: none;
    cursor: pointer;

    &:hover {
      color: black;
    }
  }

  &__field {
    margin: 0 0 16px;

    &--last {
      margin-bottom: 32px;
    }
  }
}
